@font-face {
    font-family: PostGrotesk;
    src: url(/currency-fair-frontend/static/media/PostGrotesk-Light.d8432693.woff);
    font-weight: 100;
}

@font-face {
    font-family: PostGrotesk;
    src: url(/currency-fair-frontend/static/media/PostGrotesk-Book.73496d66.woff);
    font-weight: 200;
}

@font-face {
    font-family: PostGrotesk;
    src: url(/currency-fair-frontend/static/media/PostGrotesk-Medium.b6a37764.woff);
    font-weight: normal;
}

@font-face {
    font-family: PostGrotesk;
    src: url(/currency-fair-frontend/static/media/PostGrotesk-Bold.febf5045.woff);
    font-weight: bold;
}

* {
    font-family: PostGrotesk;
    font-weight: normal;
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
    display: flex;
    z-index: 0;
    flex-direction: column;
    justify-content: flex-start;
}

.home-header {
    background-color: #282c34;
    display: flex;
    justify-content: flex-start;
    font-size: 15px;
    color: white;
    height: 70px;
    padding-left: 10%;
}

.home-main{
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    width: 100%;
}
.home-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    width: 50%;
    height: 670px;
    padding-left: 15%;
}

.next-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3daadec2;
    color: white;
    width: 70px;
    height: 50px;
    border: 1px solid #3DAADE;
    border-radius: 5px;
    cursor: pointer;
}

.next-btn:hover{
    background-color: #3DAADE;
}

.next-btn:focus{
    outline: none;
}

.footer {
    display: flex;
    justify-content: flex-end;
    width: 40.5vw;
    border-top:0.5px solid #bfc5cab5;
    color: #3DAADE;
}

.footer h5{
    font-weight: bold;
    margin-top: 15px;
}

.footer-start {
    display: flex;
    justify-self: flex-start;
    margin-right: auto;
    color: #bfc5cab5;
}
.footer-legal {
    padding-left: 20px;
}
.home-right {
    align-self: flex-end;
    border-left: 1px solid #CBCBCA;
    background-color: #f9f8f8;
    width: 45vw;
    height: 95vh;
}

.home-panel {
    display: flex;
    flex-direction: column;
    position: absolute;
    margin-left: 50px;
    margin-top: 50px;
    width: 350px;
    height: 390px;
    border: 1px solid #bfc5cab5;
    border-radius: 5px;
}

.exchange-details {
    display: flex;
    width: 100%;
    align-items: center;
    flex-grow: 1;
    border-bottom: 1px solid #bfc5cab5;
    color: #a4abaf;
    font-weight: bold;
}

.exchange-right{
    justify-content: flex-end;
}

.exchange-details h4{
    padding-left: 20px;
    width: 80%;
    font-weight: bold;
}

.exchange-details h5{
    padding-left: 10px;
    font-weight: bold;
}

.question-image {
    width: 15px;
    height: 15px;
    padding-right: 20px;
}

.last-box {
    justify-content: center;
    border-bottom: none;
    border-top: 1px solid #bfc5cab5;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    color: #ffb801;
}

.exchange-details-large {
    display: flex;
    width: 100%;
    flex-grow: 10;
    color: rgba(173, 179, 184, 0.71);
    padding-bottom: 20px;
    color: #a4abaf;
}

.exchange-details-large h4 {
    font-weight: bold;
}

.whitebg {
    background-color: white;
}
.col-1 {
    padding-left: 20px;
    width: 50%;
    height: 90%;
}

.col-2 {
    text-align: right;
    padding-right: 10px;
    width: 50%;
    height: 90%;
}

.black {
    color: #676767;
}

.blue {
    color: #3DAADE;
}
.modal {
    display: flex;
    opacity: 0;
    position: fixed;
    justify-content: center;
    align-items: center;
    z-index: -1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.66);
}

.modal-active {
    z-index: 1;
    opacity: 1;
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}

.modal-content {
    display: flex;
    flex-direction: column;
    background-color: #fefefe;
    border: 1px solid #888;
    border-radius: 5px;
    width: 620px;
    height: 500px;
}

.modal-header {
    width: 80%;
    padding: 10px 0 0 40px;
    flex-grow: 0.5;
}

.modal-top-title {
    display: flex;
    align-items: center;
    color: #737578;
}

.modal-top-title h3 {
    font-weight: bold;
}

.title-lock {
    width: 30px;
    height:30px;
    padding-bottom: 5px;
    margin-right: 10px;
}

.modal-top-info {
    color: #969BA0;
    margin: 0;
    font-weight: normal;
    font-size: 15px;
}

.modal-body{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-grow: 2;
    border-top: 1px solid #bfc5cab5;
    border-bottom: 1px solid #bfc5cab5;
}

.modal-body-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 100%;
}

.enter-code-info {
    flex-grow: 1;
    display: flex;
    align-items: center;
}

.phone-no {
    display: flex;
    align-items: center;
    border: 0.5px solid #bfc5cab5;
    border-radius: 2px;
    width: 160px;
    height: 30px;
    margin-left: 5px;
    background-color: #F9F8F5;
}

.phone-no h4{
    margin:0;
    color: #969BA0;
    font-weight: bold;
}

.phone-no-prefix {
    display: flex;
    align-items: center;
    justify-content: center;
    height:100%;
    width: 35%;
    border-right: 0.5px solid #bfc5cab5;
    background-color: #F0EFEC;
    border-radius: 2px 0 0 2px;
}

.phone-no-main {
    display: flex;
    width: 70%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.enter-code-verify {
    display: flex;
    justify-content: space-evenly;
    flex-grow: 1;
    align-items: center;
}

.verify-box {
    text-align: center;
    width: 55px;
    height: 55px;
    border: 1px solid #bfc5cab5;
    margin: 0 5px 0 5px;
    border-radius: 5px;
}

.verify-box:focus{
    border-color: #40b8ed;
    outline: none;
}

.receive {
    width: 78%;
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
}

.receive-new {
    display: flex;
    align-items: center;
}

.receive h5 {
    color: #969BA0;
    font-weight: bold;
}

.receive-call {
    width:60%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.receive-icon {
    width: 15px;
    height: 15px;
}

.modal-footer{
    flex-grow: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;
}
.footer-btn {
    border: 1px solid #bfc5cab5;
    font-size: 14px;
    font-weight: bold;
    border-radius: 5px;
    height: 35px;
    margin-left: 15px;
}
.verify-btn {
    opacity: 0.3%;
    background-color: rgba(185, 220, 255, 0.68);
    color: white;
}

.verify-btn:hover {

}

.close-btn {
    color: #818181;
    background: white;
    height: 35px;
    width: 70px;
}

.close-btn:hover,
.close-btn:focus {
    background-color: #f6f6f6;
    outline: none;
    cursor: pointer;
}

.footer-text {
    display: flex;
    justify-content: flex-end;
    width: 63%;
    color: #3DAADE;
}
.tab {
    background-color: white;
    display: flex;
    width: 41vw;
    justify-content: center;
}

.tab-links {
    display: flex;
    flex-direction: column;
    background-color: inherit;
    border-bottom: 5px solid #99ceff45;
    color: #bfc5cab5;
    width: 25%;
    padding-left: 8%;
}

.link-active {
    border-color: #3DAADE;
    color: #99ceff;
}

.tab-links h4 {
    margin-bottom: 10px;
    margin-top: 0;
    font-weight: bold;
}

.tab-links h5 {
    margin-bottom: 5px;
    margin-top: 0;
    font-weight: bold;
}
.transaction {
    background-color: white;
    display: flex;
    flex-direction: column;
}

.transaction h3 {
    margin-top: 0;
    margin-bottom: 40px;
    font-size: medium;
}

.grey {
    color: #969BA0;
}

.transaction h2 {
    font-size: larger;
    font-weight: bold;
}
.transaction-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: inherit;
    border: 1px solid #bfc5cab5;
    border-top: none;
    color: #bfc5cab5;
    height: 110px;
    width: 37vw;
    padding-left: 20px;
    padding-right: 30px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.transaction-box-text {
    display: flex;
    flex-direction: column;
}

.transaction-box h1 {
    margin: 0;
    color: black;
}

.transaction-box h3 {
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 5px;
}
.box-1 {
    background-color: #a3d6ef14;
    color: #3DAADE;
    border: 1px solid #3DAADE;
    border-radius: 5px 5px 0 0;
}

.blue {
    color: #3DAADE;
}

.currency-box {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fbfbfa;
    color: #585858;
    width: 90px;
    height: 45px;
    border: 1px solid #bfc5cab5;
    border-radius: 30px 3px 3px 30px;
}

.currency-box h4 {
    font-weight: bold;
}

.currency-img {
    height: 20px;
    width: 20px;
    margin-right: 10px;
}
